@font-face {
  font-family: 'CentraNo1';
  font-style: normal;
  font-weight: 700;
  src: url("./fonts/CentraNo1-Bold.woff2") format("woff2"), url("./fonts/CentraNo1-Bold.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: 'CentraNo1';
  font-style: italic;
  font-weight: 700;
  src: url("./fonts/CentraNo1-BoldItalic.woff2") format("woff2"), url("./fonts/CentraNo1-BoldItalic.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: 'CentraNo1';
  font-style: normal;
  font-weight: 500;
  src: url("./fonts/CentraNo1-Medium.woff2") format("woff2"), url("./fonts/CentraNo1-Medium.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: 'CentraNo1';
  font-style: normal;
  font-weight: 400;
  src: url("./fonts/CentraNo1-Book.woff2") format("woff2"), url("./fonts/CentraNo1-Book.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: 'CentraNo1';
  font-style: normal;
  font-weight: 300;
  src: url("./fonts/CentraNo1-Light.woff2") format("woff2"), url("./fonts/CentraNo1-Light.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: 'handsomeregular';
  font-style: normal;
  font-weight: 400;
  src: url("./fonts/handsomeregular.woff2") format("woff2"), url("./fonts/handsomeregular.woff") format("woff");
  font-display: swap;
}

*,
::after,
::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-family: sans-serif;
  line-height: 1.15;
}

main {
  display: block;
}

h1 {
  margin: 0.67em 0;
  font-size: 2em;
}

p + p {
  margin-top: 1rem;
}

a {
  background-color: transparent;
}

abbr[title] {
  text-decoration: underline dotted;
}

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

button,
input,
optgroup,
select,
textarea {
  border: 1px solid currentcolor;
  line-height: inherit;
}

button {
  overflow: visible;
  text-transform: none;
}

[type='button'],
[type='reset'],
[type='submit'],
button {
  padding: 1px 6px;
  appearance: button;
}

input {
  overflow: visible;
}

input,
textarea {
  padding: 1px;
}

fieldset {
  margin: 0 2px;
  border: 1px solid currentcolor;
}

legend {
  display: table;
  max-width: 100%;
  color: inherit;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

select {
  text-transform: none;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

[type='search'] {
  outline-offset: -2px;
  appearance: textfield;
}

[type='color'] {
  background: inherit;
}

::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

::input-placeholder {
  opacity: 0.5;
  color: inherit;
}

::-webkit-file-upload-button,
::-webkit-search-decoration {
  font: inherit;
  appearance: button;
}

::-moz-focus-inner {
  border: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  color: inherit;
}

dl,
ol,
ul {
  margin: 1em 0;
}

dl dl,
dl ol,
dl ul,
ol dl,
ol ol,
ol ul,
ul dl,
ul ol,
ul ul {
  margin: 0;
}

b,
strong {
  font-weight: bolder;
}

audio,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

table {
  border-color: inherit;
  text-indent: 0;
}

details {
  display: block;
}

dialog {
  display: block;
  position: absolute;
  right: 0;
  left: 0;
  width: fit-content;
  height: fit-content;
  margin: auto;
  padding: 1em;
  border: solid;
  background-color: inherit;
  color: inherit;
}

dialog:not([open]) {
  display: none;
}

summary {
  display: list-item;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

html {
  height: 100%;
  font-size: 12px;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-x: hidden;
  background-color: #fff;
  color: #2d2d2d;
  font-family: 'CentraNo1', sans-serif;
  font-weight: 400;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p,
h6,
h5,
h4,
h3,
h2,
h1 {
  margin-top: 0;
  margin-bottom: 0;
}

button {
  cursor: pointer;
}

svg,
img {
  max-width: 100%;
  max-height: 100%;
}

section {
  width: 100vw;
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  white-space: nowrap;
  clip-path: inset(50%);
}

@keyframes slide-in-right {
  0% {
    transform: translateX(1000px) skewX(30deg);
    opacity: 0;
  }

  100% {
    transform: translateX(0) skewX(0);
    opacity: 1;
  }
}

@keyframes slide-in-bottom {
  0% {
    transform: translateY(600px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes scale-in-center {
  0% {
    transform: scale(0);
    opacity: 1;
  }

  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.container {
  width: 94rem;
  max-width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: 2rem;
  padding-left: 2rem;
}

.link {
  text-decoration: none;
}

.link--primary {
  color: #2d2d2d;
}

.link--secondary {
  color: #fff;
}

.link--lg {
  font-size: 1.625rem;
}

.btn {
  display: inline-block;
  min-width: 14.16667rem;
  height: 2.83333rem;
  transition: background-color 0.5s ease-in-out;
  border-width: 0;
  border-radius: 2.5rem;
  font-family: inherit;
  font-size: 1.16667rem;
  font-weight: 500;
  line-height: 2.83333rem;
}

.btn--lg {
  min-width: 14.16667rem;
  font-size: 1.16667rem;
  line-height: 2.83333rem;
}

.btn--primary {
  background-color: #232323;
  box-shadow: 0 10px 30px rgba(17, 39, 106, 0.3);
  color: #fff;
}

.btn--accent {
  background-color: #6a8ae8;
  box-shadow: 0 2px 16px rgba(0, 30, 36, 0.3);
  color: #fff;
}

.btn:hover {
  background-color: #8fa9f4;
}

.header {
  display: none;
  position: fixed;
  z-index: 10;
  top: 0;
  right: 0;
  left: 0;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 1.875rem;
}

.intro {
  position: relative;
  background-image: linear-gradient(167.47deg, #f8f7fc 61.22%, rgba(248, 247, 252, 0) 90.91%);
}

.intro__container {
  display: flex;
  flex-direction: column-reverse;
  column-gap: 1.5rem;
}

.intro__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 1;
  padding-top: 3.5rem;
  padding-bottom: 3.5rem;
  text-align: center;
}

.intro__media {
  flex-shrink: 0;
  overflow: hidden;
}

.intro__media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.intro__media--right {
  display: none;
}

.intro__slogan {
  font-size: 1.16667rem;
  font-style: italic;
  font-weight: 700;
  line-height: 1.5rem;
  text-transform: lowercase;
}

.intro__slogan span {
  display: block;
}

.intro__slogan-accent {
  color: #6a8ae8;
}

.intro__title {
  margin-top: 2.66667rem;
  font-size: 2.08333rem;
  font-weight: 700;
}

.intro__text {
  margin-top: 2.66667rem;
  font-size: 1.16667rem;
  line-height: 1.5rem;
}

.intro__btn {
  margin-top: 4rem;
}

.services {
  display: flex;
  flex-direction: column;
  align-items: self-start;
  padding-top: 3.5rem;
  padding-bottom: 3.5rem;
  row-gap: 5rem;
}

.service {
  text-align: center;
}

.service__icon {
  width: 3rem;
}

.service__title {
  margin-top: 1rem;
  font-size: 1.33333rem;
}

.service__text {
  margin-top: 1rem;
  line-height: 1.33333rem;
}

