.overview__img {
  position: relative;
}

.overview__img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
  filter: drop-shadow(0 20px 70px rgba(38, 58, 108, 0.2));
}

.overview__icon {
  position: absolute;
  top: -7rem;
  left: 50%;
  width: 5rem;
  transform: translateX(-50%) rotate(-40deg);
}

