}

.step__title {
  margin-left: 1rem;
  font-size: 1.33333rem;
  font-weight: 400;
}

.step__title strong {
  font-weight: 700;
}

.steps__btn {
  width: 100%;
  margin-top: 6rem;
  text-align: center;
}

.tabs {
  padding-top: 1.5rem;
  padding-bottom: 4.5rem;
