  margin-top: 3rem;
  column-gap: 2rem;
}

.tab-img {
  flex-shrink: 0;
}

.tabs__info {
  display: contents;
}

.tab {
  position: relative;
  margin-bottom: 2rem;
}

.tab::before {
  content: attr(data-num);
  font-family: 'handsomeregular', sans-serif;
  font-size: 2rem;
}

.tab--active::before {
  color: #6a8ae8;
}

.tab__btn {
  border-width: 0;
  background-color: transparent;
  color: #6a8ae8;
  font-family: inherit;
  font-size: 1.33333rem;
  font-weight: 700;
  text-align: left;
}

.tab--active .tab__btn {
  color: #6a8ae8;
  font-weight: 700;
}

.tab__body {
  display: block;
  margin-top: 1rem;
}

.tab__body ul {
  margin: 2rem 0 0;
  padding: 0;
  list-style-type: none;
}

.tabs__btn {
  margin-top: 3rem;
  text-align: center;
